import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import Vuex from 'vuex'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

import routes from './routes'

Vue.use(VueResource)
Vue.use(VueRouter)
Vue.use(Vuex)
Vue.use(ElementUI, {
    size: 'small'
})

//请求会以application/x-www-form-urlencoded作为MIME type，就像普通的HTML表单一样
Vue.http.options.emulateJSON = true;
//Vue.http.options.emulateHTTP = true;

Vue.prototype.base = "http://www.form.com/api/v1";

Vue.prototype.getId = function(length = 5){
	return Number(Math.random().toString().substr(3,length) + Date.now()).toString(36);
}

//解决路由重复问题
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err);
};

const router = new VueRouter({
	base: './',
	mode: 'hash',
	routes
})

//路由过滤
router.beforeEach(function(to, froms, next) {
	if (to.meta.title) {
		document.title = to.meta.title
	}
	if (to.path === '/login') {
		next()
	} else {
		let userInfo = localStorage.getItem('userInfo')
		//alert(userInfo)
		//console.log(userInfo)
		if (userInfo === null || userInfo === '') {
			next('/login')
		} else {
			next()
		}
	}
})

//全局存储配置
const store = new Vuex.Store({
	state: {
		count: 0,
		userInfo: localStorage.getItem('userInfo') ? localStorage.getItem('userInfo') : ''
	},
	mutations: {
		increment(state) {
			state.count++
		},
		saveUserInfo(state, userInfo) {
			state.userInfo = userInfo
			localStorage.setItem('userInfo', userInfo)
		},
		doLogout(state) {
			state.userInfo = ''
			localStorage.removeItem('userInfo')
		}
	}
})

Vue.config.productionTip = false

new Vue({
	router,
	store,
	render: h => h(App),
}).$mount('#app')
