//路由配置
import Home from './components/Home.vue'

let routes = [{
		path: '/',
		name: '',
		component: Home,
		iconCls: 'el-icon-menu',
		leaf: true, //是否一级
		children: [{
			path: '/',
			component: () => import('./components/Index.vue'),
			name: '首页'
		}, ],
		meta: {
			title: '首页'
		},
		hidden: false
	},
	{
		path: '/',
		name: '表单管理',
		component: Home,
		iconCls: 'el-icon-menu',
		leaf: false,
		children: [{
			path: '/form',
			component: () => import('./components/form/Index.vue'),
			name: '表单管理'
		}, {
			path: '/form/add',
			component: () => import('./components/form/Add.vue'),
			name: '添加表单',
			hidden: true
		},{
			path: '/form/designer',
			component: () => import('./components/form/Designer.vue'),
			name: '设计表单',
			hidden: true
		}],
		meta: {
			title: '表单管理'
		},
		hidden: false
	},
	{
		path: '/',
		name: '用户管理',
		component: Home,
		iconCls: 'el-icon-menu',
		leaf: false,
		children: [{
			path: '/user',
			component: () => import('./components/user/Index.vue'),
			name: '用户列表'
		}, {
			path: '/user/add',
			component: () => import('./components/user/Add.vue'),
			name: '添加用户',
			hidden: true
		}, ],
		meta: {
			title: '用户管理'
		}
	},
	{
		path: '/login',
		name: 'Login',
		component: () => import('./components/Login.vue'),
		meta: {
			title: '登录'
		},
		hidden: true
	},
	{
		path: '/',
		component: Home,
		name: '日志管理',
		iconCls: 'el-icon-menu',
		children: [{
			path: '/log',
			component: () => import('./components/log/Index.vue'),
			name: '登录日志'
		}]
	},
	{
		path: '/404',
		component: () => import('./components/404.vue'),
		name: '',
		hidden: true
	},
	{
		path: '*',
		hidden: true,
		redirect: {
			path: '/404'
		}
	}
]

export default routes
