<template>
	<div class="container el-row">
		<div class="header el-col el-col-24">
			<div class="logo el-col el-col-10 logo-width">
				vue-admin
			</div>
			<div class="userinfo el-col el-col-4">
				<el-dropdown @command="handleCommand" trigger="click">
					<span class="el-dropdown-link userinfo-inner">
						<img src="https://thirdwx.qlogo.cn/mmopen/vi_32/uHskAsnK1e3B4yf3BJ0yAG42FF3UEwDB1y3hlWMkiauzFNBI9I22wqvbfbBCOuL0gt01sRN0yYXj45sbkbBAsDw/132">{{userInfo.username}}
					</span>
					<el-dropdown-menu slot="dropdown">
						<el-dropdown-item>个人信息</el-dropdown-item>
						<el-dropdown-item command="logout" divided>退出登录</el-dropdown-item>
					</el-dropdown-menu>
				</el-dropdown>
			</div>
		</div>

		<div class="main el-col el-col-24">
			<aside class="menu-expanded">
				<el-menu style="height: 100%;" :default-active="$route.path" class="el-menu-vertical-demo" @open="handleOpen"
				 @close="handleClose" @select="handleselect" unique-opened router background-color="#324157" text-color="#fff"
				 active-text-color="#20a0ff" :collapse="isCollapse">
					<template v-for="(item,index) in $router.options.routes" v-if="!item.hidden">
						<el-submenu :index="index+''" v-if="!item.leaf">
							<template slot="title"><i :class="item.iconCls"></i>{{item.name}}</template>
							<el-menu-item v-for="child in item.children" :index="child.path" :key="child.path" v-if="!child.hidden">{{child.name}}</el-menu-item>
						</el-submenu>
						<el-menu-item v-if="item.leaf&&item.children.length>0" :index="item.children[0].path"><i :class="item.iconCls"></i>{{item.children[0].name}}</el-menu-item>
					</template>
				</el-menu>
			</aside>
			<div class="content-container">
				<v-tags></v-tags>
				<div class="content-wrapper el-col el-col-24" style="padding: 10px 10px 20px 10px;overflow-y: scroll;height: 100%;box-sizing: border-box;overflow-x: hidden;background: #f0f0f0;">
					<div class="breadcrumb-container el-col el-col-24" v-if="$route.path !='/'">
						<!-- <strong class="title">{{$route.name}}</strong> -->
						<el-breadcrumb separator="/" class="breadcrumb-inner">
							<el-breadcrumb-item v-for="item in $route.matched" :key="item.path">
								{{ item.name }}
							</el-breadcrumb-item>
						</el-breadcrumb>
					</div>
					<transition name="router-fade" mode="out-in">
						<router-view></router-view>
					</transition>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
	import {
		mapMutations
	} from 'vuex'
	import vTags from './Tags.vue'
	let _this
	export default {
		data() {
			return {
				tagsList: [],
				isCollapse: false
			}
		},
		components: {
			vTags
		},
		computed: {
			userInfo() {
				var userInfo = this.$store.state.userInfo;
				return userInfo ? JSON.parse(userInfo) : {}
			}
		},
		created() {
			_this = this
			// 只有在标签页列表里的页面才使用keep-alive，即关闭标签之后就不保存到内存中了。
			_this.$on('tags', msg => {
				let arr = [];
				for (let i = 0, len = msg.length; i < len; i++) {
					msg[i].name && arr.push(msg[i].name);
				}
				_this.tagsList = arr;
			});
		},
		...mapMutations([
			'doLogout'
		]),
		methods: {
			handleCommand: function(command) {
				if (command == 'logout') {
					_this.$store.commit('doLogout')
					_this.$router.push('/login')
				}
			},
			handleOpen(key, keyPath) {
				console.log(key, keyPath);
			},
			handleClose(key, keyPath) {
				console.log(key, keyPath);
			},
			handleselect: function(a, b) {},
		}
	}
</script>

<style>
	.container {
		position: absolute;
		top: 0;
		bottom: 0;
		width: 100%;
	}

	.container .header {
		height: 60px;
		line-height: 60px;
		background: #242f42;
		/* border-bottom: 5px solid #72bff3; */
		color: #fff;
	}

	.container .header .logo-width {
		width: 230px;
	}

	.container .header .logo {
		font-size: 22px;
		padding-left: 20px;
		padding-right: 20px;
		border-right-width: 0px;
		border-right-style: solid;
		background-size: 60%;
		text-align: left;
	}

	.container .header .userinfo {
		text-align: right;
		padding-right: 35px;
		float: right;
	}

	.container .header .userinfo .userinfo-inner {
		cursor: pointer;
		color: #72bff3;
	}

	.container .header .userinfo .userinfo-inner img {
		width: 40px;
		height: 40px;
		border-radius: 20px;
		margin: 10px 0 10px 10px;
		float: right;
	}

	.container .main {
		display: flex;
		position: absolute;
		top: 60px;
		bottom: 0;
		overflow: hidden;
	}
	
	.el-menu-vertical-demo:not(.el-menu--collapse){
		width: 230px;
	}
	
	.content-collapse {
		left: 65px;
	}

	.container .main .menu-expanded {
		flex: 0 0 230px;
		width: 230px;
		text-align: left;

		/* display: block;
		position: absolute;
		left: 0;
		top: 0px;
		bottom: 0;
		overflow-y: scroll; */
	}

	.container .main .content-container {
		flex: 1;
		padding: 0px;
		position: absolute;
		left: 230px;
		right: 0;
		height: 100%;
		padding-bottom: 30px;
	}

	.container .main .content-container .breadcrumb-container {
		float: initial;
		margin: 10px 0;
	}

	.container .main .content-container .breadcrumb-container .title {
		width: 200px;
		float: left;
		color: #475669;
		font-size: 14px;
		text-align: left;
	}

	.container .main .content-container .breadcrumb-container .breadcrumb-inner {
		/* float: right; */
		margin-top: 3px;
	}

	.collapse-btn {
		float: left;
		padding: 0 20px;
		cursor: pointer;
		line-height: 60px;
	}
</style>
